.Class {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
}

.Class__Image {
  height: 300px;
  background-repeat: no-repeat;
  background-size: cover;
}

.Class__Body {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  gap: 1rem;
  padding: 1rem;
}

.Class__Title {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  gap: 2px;
}

.Class__Title__Main {
  font-size: 1.4rem;
  font-weight: bold;
  color: var(--tg-theme-text-color);
}

.Class__Title__Subtitle {
  color: var(--tg-theme-text-color);
}

.Class__Text {
  color: var(--tg-theme-text-color);
}

.Class__Coach {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  gap: 1rem;
}

.Class__Coach__Photo {
  width: 60px;
  height: 60px;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 1rem;
}

.Class__Coach__Description {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.Class__Coach__Description__Name {
  font-weight: bold;
  font-size: 1.1em;
}

.Class__Coach__Description__Archievements {
  font-weight: lighter;
  font-size: 1em;
}

.Class__Duration {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  gap: 1rem;
}

.Class__Section__Title {
  font-weight: 600;
}

.Class__Schedule {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
